<template lang="pug">
  .small
    pie-chart(
       :chart-data="datacollection"
    )
</template>

<script>
  import PieChart from './chartDepartments.js'
  export default {
    components: {
      PieChart
    },
    data () {
      return {
        datacollection: {},
        dataLabels: this.chartData.map(ele=>ele.departmentDto.name),
        data: this.chartData.map(ele=>ele.count)     
      }
    },
    mounted(){
      this.fillData()
    },
    props: {
      chartData: Array
    },
    methods: {
      fillData(){
        this.datacollection = {
          labels: this.dataLabels,
          datasets: [
            {
               backgroundColor: [
                '#346f79',
                '#fead00',
                '#80000d',
                '#555555',
                '#bf3300',
                '#b97b2a',
                '#6e3b00',
                '#171442'
            ],
              data: this.data,
             /* options: { 
                  legend: {
                    labels: {
                    fontColor: "white",
                    fontSize: 18
                    }
                  }
              },*/
            borderWidth: 2
            }
          ]     
        }
      }
    }
  }
</script>

<style lang="scss">
  .small {
    width: 100%;
    margin:  150px auto;
    background: #ffe9ce;
